/* –ü–æ–ª–Ω–æ–µ –ø–æ–∫—Ä—ã—Ç–∏–µ —ç–∫—Ä–∞–Ω–∞ */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
    overscroll-behavior: none; /* –û—Ç–∫–ª—é—á–∞–µ—Ç –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –ø—Ä–∏ —Å–≤–∞–π–ø–µ –≤–Ω–∏–∑ */
    touch-action: none; /* –û—Ç–∫–ª—é—á–∞–µ—Ç —Å–≤–∞–π–ø—ã */
}

/* –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –≤—ã–¥–µ–ª–µ–Ω–∏—è —Ç–µ–∫—Å—Ç–∞ */
* {
    user-select: none; /* –ó–∞–ø—Ä–µ—â–∞–µ—Ç –≤—ã–¥–µ–ª–µ–Ω–∏–µ */
    -webkit-user-select: none; /* –î–ª—è Safari */
    -moz-user-select: none; /* –î–ª—è Firefox */
    -ms-user-select: none; /* –î–ª—è IE/Edge */
}

/* –ì–ª–∞–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
}


/* –ö–æ–Ω—Ç–µ–Ω—Ç */
.scrollable-content {
    flex: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* –ù–∏–∂–Ω—è—è –ø–∞–Ω–µ–ª—å –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
.bottom-nav {
    width: 230px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ */
    height: 50px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
    background: #141414;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%); /* üî• –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –ø–∞–Ω–µ–ª—å */
    border-radius: 60px;
    box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.2);
}

/* –ê–∫—Ç–∏–≤–Ω—ã–π –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä */
.active-indicator {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 33.33%; /* –¢–æ—á–Ω–æ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –∫–Ω–æ–ø–∫–∞–º–∏ */
    height: 100%;
    background: #2c2c2c; /* –¶–≤–µ—Ç –∞–∫—Ç–∏–≤–Ω–æ–≥–æ –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–∞ */
    border-radius: 50px; /* –ü–æ–¥–≥–æ–Ω—è–µ–º –ø–æ–¥ –∫–Ω–æ–ø–∫—É */
    transition: left 0.3s ease-in-out;
}

/* –ö–Ω–æ–ø–∫–∏ –º–µ–Ω—é */
.bottom-nav button {
    background: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 0;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 10;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∫–æ–Ω–æ–∫ */
.button-icon {
    width: 30px;
    height: 30px;
    filter: brightness(0.7);
    transition: filter 0.3s ease-in-out, transform 0.2s ease-in-out;
}

/* –í—ã–¥–µ–ª–µ–Ω–Ω–∞—è –∫–Ω–æ–ø–∫–∞ */
.bottom-nav button.active .button-icon {
    filter: brightness(2) contrast(1.2);
    transform: scale(1.1);
}

/* –§–æ–Ω —á–∞—Å—Ç–∏—Ü */
.background-effect {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none; /* –ß–∞—Å—Ç–∏—Ü—ã –Ω–µ –º–µ—à–∞—é—Ç –∫–ª–∏–∫–∞–º */
    z-index: -1; /* –ü–æ–¥ –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º */
    background-color: black;
    overflow: hidden;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —á–∞—Å—Ç–∏—Ü */
.particles-container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

/* –ß–∞—Å—Ç–∏—Ü—ã */
.particle {
    position: absolute;
    width: 6px;
    height: 6px;
    background-color: #fff; /* –°–µ—Ä—ã–µ —Ç–æ—á–∫–∏ */
    border-radius: 50%;
    animation: rise linear infinite;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è —á–∞—Å—Ç–∏—Ü (–±—É–¥–µ—Ç –∑–∞–¥–∞–≤–∞—Ç—å—Å—è –¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏) */
@keyframes rise {
    from {
        transform: translateY(100vh);
        opacity: 0;
    }
    to {
        transform: translateY(-10vh);
        opacity: 1;
    }
}


